@import './base';
@import './animation';
@import './gallery';



body {
  background-color: $primary-color;
  overflow-x: hidden;
  
}


.container{
  margin: auto;
  padding: 2rem;
  width: 80%;
  overflow: hidden;
}


.loading{
  position: fixed;
  top:0;
  z-index: 999;
  width: 100vw;
  height: 100vh;
}

.overlay{
  width: 100%;
  height: 100%;  
  position: relative;
  top: 0;
  z-index: 999;
  bottom: 0;
  background-color: $black-color;
}

.big_logo{
  font-size: 20rem;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  color: transparent;
   -webkit-text-stroke-width: 0.5px;
  -webkit-text-stroke-color: $black-color;
}

.logo_container{
   position: absolute;
  top: 50%;
  left: 50%;
  display: inline-block;
  transition: opacity 0.5s;
   transform: translate(-50%, -50%);
}

.animated_cursor {
      border-style: solid;
      border-color: transparent transparent gray transparent;
      border-width: 0 25px 25px 25px;
      height: 0;
      opacity: 0;
      width: 50px;
      box-sizing: content-box;
      position: relative;
      top: 50%;
      right: 50%;
      margin: 20px 0 40px 0;
}

.animated_cursor::after{
 content: "";
      position: absolute;
      top: 25px;
      left: -25px;
      color: $white-color;
      width: 0;
      height: 0;
      border-style: solid;
      border-color: gray transparent transparent transparent;
      border-width: 60px 50px 0 50px;
}

.animated_cursor--left {
      border-style: solid;
      border-color: transparent transparent gray transparent;
      border-width: 0 25px 25px 25px;
      height: 0;
      opacity: 0;
      width: 50px;
      box-sizing: content-box;
      position: relative;
      margin: 20px 0 40px 0;
}

.animated_cursor--left::before{
  content: "Previous";
  position: absolute;
  font-size: 1.5rem;
  color: $white-color;
  display: block;
  
  top:100%;
  left: 40%;
  transform: translate(-50%, -50%);
  height: 100%;
  width: 100%;
}


.animated_cursor--left::after{
 content: "";
      position: absolute;
      top: 25px;
      left: -25px;
     
      color: $white-color;
      width: 0;
      height: 0;
      border-style: solid;
      border-color: gray transparent transparent transparent;
      border-width: 60px 50px 0 50px;
}

.animated_cursor--right {
      border-style: solid;
      border-color: transparent transparent gray transparent;
      border-width: 0 25px 25px 25px;
      height: 0;
      opacity: 0;
      width: 50px;
      box-sizing: content-box;
      position: relative;
      margin: 20px 0 40px 0;
}

.animated_cursor--right::before{
  content: "Next";
  position: absolute;
  font-size: 1.5rem;
  color: $white-color;
  display: block;
  top:100%;
  left: 50%;
  text-align: center;
  transform: translate(-50%, -50%);
  height: 100%;
  width: 100%;
}


.animated_cursor--right::after{
 content: "";
      position: absolute;
      top: 25px;
      left: -25px;
     
      color: $white-color;
      width: 0;
      height: 0;
      border-style: solid;
      border-color: gray transparent transparent transparent;
      border-width: 60px 50px 0 50px;
}



header{
  position: relative;
  top: 0;
  z-index: 1;
  
  .logo{
  color: $black-color;
  position: absolute;
  top: 50px;
  display: block;
  opacity: 0;
  will-change: opacity;
  transition: opacity 0.5s;
  text-decoration: none;
  left: 48.3%;
  transform: translate(-50%, -50%);
  letter-spacing: 1rem;
  font-size: 3rem;
  }

nav{
  position: fixed;
  top: 100px;
 
  .list{
  left: 0;
  opacity: 0;
 position: absolute;
  width: auto;
   display: block;

   z-index: 5;
    line-height: 0;
   height: auto;
   list-style: none;
   white-space: nowrap;
   transform-origin: right top;
   transform: translateX(-85%) rotate(-90deg);
    
    .link{
      margin-right: 3rem;
      display: inline-block;
      font-size: 1rem;  
      font-size: 1.2rem; 
      color: $black-color;
      text-transform: uppercase;
      font-weight: 700;
    } 
    
    .link a{
      color: $black-color;
      text-decoration: none;
      letter-spacing: 0.5rem;
    font-weight: lighter;
      
      
    }




  }
}




    .list-right{
      position: fixed;
      right: 15rem;
      top: 5rem;
      z-index: 5;
      list-style: none;
      white-space: nowrap;
      display: block;
      font-size: 1.2rem; 
      line-height: 0;
    
    
      .link{
        display: inline-block;
        margin-right: 3rem;
     
        a{
          color:$white-color;
          letter-spacing: 0.5rem;
          text-transform: uppercase;
          text-decoration: none;
         font-weight: normal;
         transition: opacity 0.5s;
        }
      }
    }
}


