.heading{
  width: 800px;
  font-family: 'vollkorn';
  line-height: 6rem;
  height: 700px;
  .grid{
  display: grid;
  grid-template-columns: repeat(14,50px);
  grid-template-rows: repeat(14,50px);
  justify-items: start;
  align-items: start;

  .behind-overlay{
    background-color: $secondary-color;
    width: 20rem;
    align-items: center;
    justify-items: center;
    height: 20rem;
    display: grid;
    grid-row: 4/5;
    grid-column: 6/8;
  }

    h1{
    
      font-size: 4rem;
      grid-column: 7/-1;
      grid-row: 6;
      font-weight: bolder;
      color: $black-color;
      }

      .cta-shop{
        display: grid;
   
        grid-column: 7;
        grid-row: 9;
        justify-self: center;
        margin-top: 3.5rem;
        pointer-events: all;
        
        .button_broken{
          width: 8rem;
          height: 4rem;
          background-color: transparent;
          border: 1px solid $tertiary-color;
          position: relative;
          top: 0;
          right: -40px;
        }
        
 button{
    cursor: pointer;
    text-transform: uppercase;
    outline: none;
    letter-spacing: 0.7rem;
    padding: 1rem 3rem;
    border: none;
    position: absolute;
    background-color: transparent;
    font-size: 2rem;
      }
    }
  }
}


.arrow-down{
  display: grid;
  grid-row: -1;
  grid-column: 8;
img{
  display: grid;
  grid-row: 15;
  width: 90%;
  height: auto;
  transform: rotate(90deg);
  filter: invert(0%) sepia(3%) saturate(1%) hue-rotate(0deg) brightness(100%) contrast(50%);
}  
}

}


