.cart{
   z-index: 777;
    position: fixed;
    height: 100vh;
    width: 100vw;
    top: 0;
    left: 0;
    display: none;
    
    .overlay_prop{
      position: relative;
      height: 100%;
      width: 100%;
      background: $primary-color;
      
      .cart_heading{
        color: $black-color;
        font-weight: 700;
        font-size: 4rem;
        margin-bottom: 3rem;
      }


      .cart_container{
      
        display: grid;
        grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
      }

      .cart_footer{
        height: 7rem;
        width: 100%;

        background-color: $white-color;
        position: fixed;
        bottom: 0;
        color: $black-color;
        display: flex;
        justify-content: space-around;
        text-transform: uppercase;

        h3, a{
          font-size: 2rem;
          font-weight: 400;
        }

        .cart_total{
          flex: 4;
          color: $white-color;
          background-color: #2c2c2c;
          padding: 2rem; 
        }

        .cart_options{
           flex: 1;
          background-color: $white-color;

&--checkout{
  display: flex;
  justify-content: center;
  height: 100%;
  align-items: center;
  font-size: 1.8rem;
  align-self: center;
  text-decoration: none;
  color: $black-color;
  position: relative;
  background: $black-color;
  transition: all .35s;


&:after{
  position: absolute;
  content: "";
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: $white-color;
  transition: all .35s;
}

&:hover{
  background: $black-color;
  color: $white-color;
}

&:hover:after{
  height: 0%;
}



  & label{
    position: relative;
    z-index: 999;
  }
}






        }
      }
    }
}


