    align-items: center;

    &__left{
      padding: 7rem 8rem 7rem 20rem;
      &--carousel{
        img{
          height: 26.7rem;
          width: 33.3rem;
          position: relative;
          z-index: -1;
        }
      }
    }

    &__right{
      width: 50%;
      height: 100%;
      padding: 0 6rem;
      

        &--carousel{
          img{
            height: 100%;
            position: relative;
            z-index: -1;
            width: 100%;
          }
        }
    }
  }
}